/* ============================================= */
/*              Navigation                       */
/* ============================================= */

.nav {
  padding-left: 0;
  flex-basis: 100%;
  display: flex;
  flex-direction: column;

  @include media-query {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;

  }

  &-link {
    text-align: center;
    border-radius: 5px;
    padding: 10px 0;
    display: block;
    width: 100%;
    transition:
      background-color .2s ease-in-out,
      color .2s ease-in-out;

      @include media-query  {
      
        flex-basis: 100px;
      
    }
  }

  &-link:hover {
    color: $white;
    background-color: $color-prim;
  }

}