// Descriptive color valuables

$grey: #777;
$color-prim: #58e1c1;
$color-sec: #51ddfc;
$white: #fff;
$light-grey: #ccc;


$button-colors: (default: #51ddfc,
    error: #e4757a,
    info: #927bc1,
    success: #63cc82,
    warning: #fd7856);

// Grid colors

$grid-colors: (1: #927bc1,
    2: #fd7856,
    3: #deb2ee,
    4: #7ee6fd,
    5: #88d9a0,
    6: #4da2bb,
    7: #ec9fa3,
    8: #fd9f85,
    9: #e4757a,
    10: #63cc82,
    11: #51ddfc,
    12: #80e8d1);


// Base
$base-line-height: 1.45em;



// Font

$main-font: 'Lato',
sans-serif;
$base-font-weight: 300;
$link-font-weight: 700;


// Media queries

$brk-point: 768px;